import React from "react";
import Link from "next/link";

function Footer3() {
  return (
    <footer className="main-footer footer-style-three">
      <div className="widgets-section">
        <div className="anim-icons">
          <div className="image-1 circleZoom">
            <img src="/assets/images/icons/theme-icon39.png" alt="Theme Icon" />
          </div>
        </div>

        <div className="container">
          <div className="row">
            <div className="footer-column col-lg-4 col-md-6">
              <div className="footer-widget about-widget">
                <div className="logo">
                  <img src="/assets/images/logo-2.png" alt="Company Logo" />
                </div>
                <div className="widget-content">
                  <div className="text">
                    Wir helfen Unternehmen dabei, digital zu wachsen. Mit
                    Strategie, Design und messbaren Ergebnissen.
                  </div>
                  <div className="social-widget">
                    <ul className="social-icon-list1">
                      <li>
                        <Link
                          href="https://twitter.com"
                          target="_blank"
                          aria-label="Twitter"
                        >
                          <i className="fab fa-twitter" />
                        </Link>
                      </li>
                      <li>
                        <Link
                          href="https://facebook.com"
                          target="_blank"
                          aria-label="Facebook"
                        >
                          <i className="fab fa-facebook-f" />
                        </Link>
                      </li>
                      <li>
                        <Link
                          href="https://pinterest.com"
                          target="_blank"
                          aria-label="Pinterest"
                        >
                          <i className="fab fa-pinterest-p" />
                        </Link>
                      </li>
                      <li>
                        <Link
                          href="https://instagram.com"
                          target="_blank"
                          aria-label="Instagram"
                        >
                          <i className="fab fa-instagram" />
                        </Link>
                      </li>
                    </ul>
                  </div>
                </div>
              </div>
            </div>
            <div className="footer-column col-lg-2 col-md-6">
              <div className="footer-widget links-widget">
                <h5 className="widget-title">Schnellzugriffe</h5>
                <ul className="user-links">
                  <li>
                    <Link href="/">Über uns</Link>
                  </li>
                  <li>
                    <Link href="/">Unser Team</Link>
                  </li>
                  <li>
                    <Link href="/">Unser Portfolio</Link>
                  </li>
                  {/* <li>
                    <Link href="/">Careers</Link>
                  </li> */}
                  <li>
                    <Link href="/">Kontakt</Link>
                  </li>
                  <li>
                    <Link href="/">Fallstudien</Link>
                  </li>
                </ul>
              </div>
            </div>
            <div className="footer-column col-lg-3 col-md-6">
              <div className="footer-widget links-widget">
                <h5 className="widget-title">Unsere Leistungen</h5>
                <ul className="user-links">
                  <li>
                    <Link href="/">Webentwicklung</Link>
                  </li>
                  <li>
                    <Link href="/">UI/UX Design</Link>
                  </li>
                  <li>
                    <Link href="/">Mobile Anwendungen</Link>
                  </li>
                  <li>
                    <Link href="/">Digitales Marketing</Link>
                  </li>
                  <li>
                    <Link href="/">SEO-Optimierung</Link>
                  </li>
                  <li>
                    <Link href="/">E-Commerce-Lösungen</Link>
                  </li>
                </ul>
              </div>
            </div>
            <div className="footer-column col-lg-3 col-md-6">
              <div className="footer-widget links-widget">
                <h5 className="widget-title">Kontakt</h5>
                <div className="widget-content">
                  <div className="contact-info-box">
                    <div className="icon-box">
                      <i className="fa fa-envelope" />
                    </div>
                    <div className="content-box">
                      <h6 className="title">E-Mail:</h6>
                      <p>
                        <Link
                          href="mailto:info@axishorizonglobal.com"
                          className="link"
                        >
                          info@axishorizonglobal.com
                        </Link>
                      </p>
                    </div>
                  </div>
                  <div className="contact-info-box">
                    <div className="icon-box">
                      <i className="fa fa-map-marker-alt" />
                    </div>
                    <div className="content-box">
                      <h6 className="title">Adresse:</h6>
                      <p>Vulkanstraße 12 C, 10365 Berlin</p>
                    </div>
                  </div>
                  <div className="contact-info-box">
                    <div className="icon-box">
                      <i className="fa fa-phone" />
                    </div>
                    <div className="content-box">
                      <h6 className="title">Telefon:</h6>
                      <p>
                        <Link href="tel:1234567890" className="link">
                          +49 000 000000
                        </Link>
                      </p>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
          <div className="footer-bottom">
            <div className="container">
              <div className="inner-container justify-content-center">
                <p className="copyright-text">
                  © {new Date().getFullYear()} All rights reserved by
                  Digitaal.com
                </p>
              </div>
            </div>
          </div>
        </div>
      </div>
    </footer>
  );
}

export default Footer3;
